@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,*::before,*::after{box-sizing:inherit}html{box-sizing:border-box;overflow-x:hidden;--primary-color: #770000;--secondary-color: #020077;--white: #fff;--light-gray:#b8b8b8;--dark-gray: #0e0e0e;--black: #0c0c0c;--error-color: #eb5757;--primary-font: "Consolas", "IBM Plex Mono", monospace;--secondary-font: poppins;--global-font-size: 16px}@media(max-width: 600px){html{--global-font-size: 18px}}.app{display:flex;flex-direction:column}header{flex:0 0}main{position:relative;flex:1 0 200px;display:flex;flex-direction:column;padding:0 50px}@media(max-width: 768px){main{padding:0}}h1,h2,h3,h4{font-family:"Consolas", "IBM Plex Mono", monospace;font-family:var(--primary-font);color:#fff;color:var(--white)}p{color:#b8b8b8;color:var(--light-gray)}body{background:#0e0e0e;background:var(--dark-gray);color:#b8b8b8;color:var(--light-gray);font-family:"Consolas", "IBM Plex Mono", monospace;font-family:var(--primary-font);font-size:16px}.app{min-height:100vh;display:flex;flex-direction:column}@-webkit-keyframes rotate20{100%{-webkit-transform:rotateZ(-20deg) scale(1.2);transform:rotateZ(-20deg) scale(1.2)}}@keyframes rotate20{100%{-webkit-transform:rotateZ(-20deg) scale(1.2);transform:rotateZ(-20deg) scale(1.2)}}
